import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams  ,MenuController} from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { User } from "../../models/user";
import { AngularFireAuth } from 'angularfire2/auth';

import { UsercrudProvider } from './../../providers/usercrud/usercrud';
import { FirstConnectionPage } from "../../pages/first-connection/first-connection";
import { LoginPage } from '../login/login';

@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {

  user = {} as User;

  signUpForm: FormGroup;

  submitAttempt: boolean = false;

  verifPassword: string;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private afAuth : AngularFireAuth,
    private userCrudProvider: UsercrudProvider,
    private menu: MenuController,
    public formBuilder: FormBuilder) {

      this.signUpForm = formBuilder.group({
        prenom: ['',Validators.compose([Validators.required,Validators.pattern('[a-zA-Z1-9 ]*')])],
        nom: ['',Validators.required],
        displayName: ['',Validators.required],
        mail: ['',Validators.required],
        password: [''],
        confirmPassword: ['',Validators.required],
    });
      this.user.connectionType = "mail";
  }

  async register(user: User){
    if(this.user.password != this.verifPassword){
      alert("Mot de passe different ! ")
    }else{
      try {
        const result = await this.afAuth.auth.createUserWithEmailAndPassword(
          user.email,user.password
        ).then((res)=>{
          if(res){
            user.id = res.uid;
            user.reputation = 1;          
            user.imageUrl = "data:image/png;base64,";
            this.userCrudProvider.addUser(user);
            const userSign = this.afAuth.auth.signInWithEmailAndPassword(this.user.email,this.user.password);
            this.navCtrl.setRoot(FirstConnectionPage);
          }
        });
      } catch (error) {
        alert(error);
      }
    }
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignupPage');
  }

  ionViewDidEnter() {
    this.menu.swipeEnable(false);
  }

  ionViewWillLeave() {
    this.menu.swipeEnable(true);
   }

   goToLoginPage(){
    this.navCtrl.setRoot(LoginPage);
   }

   verifChamps(){

    if ((this.user.email && this.user.email.trim() != '') && (this.user.password && this.user.password.trim() != '') && (this.user.displayName && this.user.displayName.trim() != '') ){
      return true;
    }else{
      return false
    }
   }

}



